  margin: 0;
  font-size: 1rem;
  line-height: 1.375;
  letter-spacing: normal;
  font-weight: 400;
  background-color: #f9dfdd;
  box-sizing: border-box;
  min-height: 70px;
  padding: calc((70px - 1.5em) / 2) 12px;
  padding-left: 58px;
